.card{
  outline: none;
  border: none;
  width: 320px;
  height: 450px;
  margin-top: 80px;
  overflow: hidden;
  box-shadow: 0px 5px 20px rgba(0,0,0,.5);
  position: relative;
  left: 50px;
  font-family: 'Signika', sans-serif;
  border-radius: 15px;


}
.data .boxer {
  /* position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%,-50%); */
  width: 320px;
  height: 450px;
  background: #fff;
  overflow: hidden;
  /* box-shadow: 0px 5px 20px rgba(0,0,0,.5); */
}

.data .boxer:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #4e73df;
  -webkit-clip-path: circle(150px at 80% 20%);
          clip-path: circle(150px at 80% 20%);
  transition: 0.5s ease-in-out;
}

.data .boxer:hover:before {
  -webkit-clip-path: circle(300px at 80% -20%);
          clip-path: circle(300px at 80% -20%);
}

.data .boxer:after {
  content: "APSSDC";
  position: absolute;
  top: 55%;
  left: -2%;
  font-size: 5.5em;
  font-weight: 800;
  font-style: italic;
  color: rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.data .boxer .imgBx {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  width: 100%;
  height: 100%;
  transition: .5s;
}

.data .boxer:hover .imgBx {
  top: 0%;
  transform: translateY(-25%);
  /* bug  */
}

.data .boxer .imgBx img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /*rotate(20deg)*/
  border-radius: 50%;
  box-shadow: 0px 4px 10px rgba(0,0,0,.4);
  width: 220px;
}

.data .boxer .contentBx {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  text-align: center;
  transition: 1s;
  z-index: 99999;
}

.data .boxer:hover .contentBx {
  height: 195px;
}
.data .boxer:hover .contentBx h1, .data .boxer:hover .contentBx h3, .data .boxer:hover .contentBx h2, .data .boxer:hover .contentBx button{
  opacity: 1;
  visibility: visible;
  transition-delay: .3s;
} 
.data .boxer .contentBx h1{
  font-size: 30px;
  transition: .5s;
  opacity: 0;
  visibility: hidden;

}

.data .boxer .contentBx h3{
  font-size: 20px;
  transition: .5s;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;

}
.data .boxer .contentBx .email{
  font-size: 15px;

}

.data .boxer .contentBx h2{
  font-size: 18px;
  transition: .5s;
  opacity: 0;
  visibility: hidden;

}

.data .boxer .contentBx button{
  transition: .5s;
  opacity: 0;
  visibility: hidden;
  letter-spacing: 1px;
  width: 90px;
}
.link:after{
  content: '';
  position: absolute;
  left: 34%;
  display: block;
  width: 0;
  height: 2px;
  transition: width .3s;
  background: #4e73df;

}
.link:hover::after{
  width: 33%;
  transition: width .3s;
}
.link:hover{
 color: #000;
 text-decoration: none;
}
.link1:after{
  content: '';
  position: absolute;
  left: 17%;
  display: block;
  width: 0;
  height: 2px;
  transition: width .3s;
  background: #4e73df;

}
.link1:hover::after{
  width: 66%;
  transition: width .3s;
}
.link1:hover{
 color: #000;
 text-decoration: none;
}
